'use strict';

angular.module('dashboardApp', ['ui.router', 'LocalStorageModule', 'ngIdle', 'angular-click-outside', 'ngCookies', 'angular.filter', 'tableSort', 'ngSanitize']).value('credentials', {}).constant('APP_CONST', {
    APP_NAME: 'Analytics app - CI dashboard',
    WS_API: '/api',
    LOGGED_IN_USER: 'loggedInUser'
}).config(function (localStorageServiceProvider, IdleProvider, KeepaliveProvider, TitleProvider) {
    localStorageServiceProvider.setStorageType('sessionStorage').setPrefix('rpm');

    // configure Idle settings
    IdleProvider.idle(45 * 60); // in seconds
    IdleProvider.timeout(5); // in seconds
    KeepaliveProvider.interval(2); // in seconds
    TitleProvider.enabled(false); // it is enabled by default

    // $cookiesProvider.defaults.domain = "https://analyticapps.global.hosting.pwc.com/appkit/";
}).controller('AppController', function ($scope, $rootScope, $timeout, $log, $window, Idle, utilService) {
    var vm = this;

    // $log.log('%c Appkit-Dev-Pre-v2.0.0 ', 'background: #fff; border: 1px solid #d04a02; color: #d04a02');
    $log.log('%c RPM-Prod-v2.0.0 ', 'background: #fff; border: 1px solid #d04a02; color: #d04a02');
    $log.log('AppController is running...');

    vm.init = function () {
        $log.log('Initialized...');
    };

    var cleanStateChangeEvent = $rootScope.$on("$stateChangeStart", function (event, next) {
        $log.log('locationChangeStart: ' + $window.location);
        if (next.name.indexOf("disclaimer") === -1) {
            // no need security check for disclaimers
            checkSecurity(event);
        }
    });

    $scope.$on('IdleTimeout', function () {
        // the user has timed out (meaning idleDuration + timeout has passed without any activity)
        // this is where you'd log them
        $log.log('Your session has expired...');
        Idle.unwatch();
        utilService.clearSession();
        utilService.go("content");
    });

    $scope.$on('$destroy', function () {
        // the user has timed out (meaning idleDuration + timeout has passed without any activity)
        // this is where you'd log them
        cleanStateChangeEvent();
    });

    checkSecurity();

    // if there is a session existed, the idle need to rewatch again.
    Idle.watch();

    /* $scope.$on('IdleStart', () => {
        // the user appears to have gone idle
        $log.log('IdleStart');
    });
      $scope.$on('IdleWarn', (e, countdown) => {
        // follows after the IdleStart event, but includes a countdown until the user is considered timed out
        // the countdown arg is the number of seconds remaining until then.
        // you can change the title or display a warning dialog from here.
        // you can let them resume their session by calling Idle.watch()
        $log.log(`IdleWarn: ${countdown}`);
    });*/

    /* $scope.$on('IdleEnd', () => {
        // the user has come back from AFK and is doing stuff. if you are warning them, you can use this to hide the dialog
        $log.log('IdleEnd');
    });
      $scope.$on('Keepalive', () => {
        // do something to keep the user's session alive
        $log.log('Keepalive');
    });*/

    function checkSecurity(event) {
        $log.log('checkSecurity called...');
        // ignore security checking for login page.
        var path = $window.location.hash; // #/login

        // Going to login page will clear all session data.
        if (path === "#/content") {
            Idle.unwatch();
            return;
        }

        // All other pages should be checked if the session existed.
        var isSecured = utilService.checkSession();

        if (!isSecured) {
            if (event) {
                event.preventDefault();
            }

            $timeout(function () {
                utilService.clearSession();
                // utilService.go("login");
                // $location.path("/login");
                Idle.unwatch();
                $window.location.hash = '#/content';
            }, 100);
            return;
        }
    }
});
'use strict';

angular.module('dashboardApp').component('appHeader', {
    templateUrl: 'app/components/header/header.html',
    bindings: { $router: '<' },
    controller: 'HeaderController'
}).controller('HeaderController', function ($log, localStorageService, utilService, Idle, APP_CONST) {
    var vm = this;

    vm.user = localStorageService.get(APP_CONST.LOGGED_IN_USER);

    vm.$log = $log;
    vm.appName = APP_CONST.APP_NAME;

    vm.currentTabIndex = 0;
    vm.isSelectLanguage = false;
    vm.isUserSetting = false;

    vm.getHighlightedTab = function (tabIndex) {
        return vm.currentTabIndex === tabIndex ? 'active' : '';
    };

    vm.switchPage = function (tabIndex) {
        vm.currentTabIndex = tabIndex;
    };

    /* User Setting Dropdown - Starts */
    vm.userSettingToggle = function () {
        vm.isUserSetting = !vm.isUserSetting;
    };
    /* User Setting Dropdown - Ends */

    vm.hideUserSettingDpdwn = function () {
        vm.isUserSetting = false;
    };
    /* Hide Dropdown in Click Outside - Ends */

    vm.logout = function () {
        utilService.ajax({
            method: 'get',
            url: APP_CONST.WS_API + '/logout'
        }, function () {
            Idle.watch();
        }, function () {
            // ignore
        }, function () {
            localStorageService.clearAll();
            utilService.go("logout");
        });
    };

    vm.gotoLogin = function () {
        utilService.go("content");
    };
});
