angular.module('dashboardApp').controller('ContentController', ContentController);

/** @ngInject */
function ContentController($http, $timeout, $log) {
    var vm = this;
    vm.jobList = [];
    vm.init = function () {
        getJenkinsData();
    };

    var getJenkinsData = function getJenkinsData() {

        $http.get("app/data/job.data.json").then(function (response) {
            $log.log(response);
            vm.jobList = [];
            _.forEach(response.data.jobs, function (job) {
                if (job.builds[0] && job.color !== 'disabled') {
                    var jobObject = {
                        displayName: job.displayName,
                        projectName: job.projectName,
                        environment: job.environment,
                        timestamp: job.builds[0].timestamp,
                        build: job.builds[0],
                        lastTriggerName: job.builds[0].culprits[0].fullName,
                        color: job.color
                    };
                    vm.jobList.push(jobObject);
                }
            });

            vm.jobList = _.sortBy(vm.jobList, "timestamp");

            //refresh job list every 10s
            // $timeout(function () {
            //     getJenkinsData();
            // }, 10000);
        }, function (err) {
            $log.error("Get Jenkins data error!");
        });

        return true;
    };
}
