angular.module('dashboardApp').factory('utilService', utilService);

function utilService($q, $http, $state, $window, $log, $rootScope, localStorageService, APP_CONST) {
    var util = {
        ajax: function ajax(ajaxSettings, successFunc, failedFunc, alwaysFunc) {
            $rootScope.showLoading = true;

            // var sequence = new Date().getTime() + "-" + parseInt(Math.random() * 1000, 10);

            $q.when($http(ajaxSettings)).then(function (resp) {
                $log.log(resp.config.method + ", url: " + resp.config.url);
                $log.log(resp);

                var d = resp.data;

                if (d.status === "FAILED") {
                    $log.log("Failed: " + d.messageBean.message);

                    // $window.alert(d.messageBean.message);

                    if (angular.isFunction(failedFunc)) {
                        failedFunc(d);
                    }

                    return;
                }

                if (angular.isFunction(successFunc)) {
                    successFunc(d);
                }

                $log.log("Success: " + angular.toJson(d.data));
            }, function (errResp) {
                if (angular.isFunction(failedFunc)) {
                    failedFunc(errResp);
                }

                $log.log("Error: " + angular.toJson(errResp));
            }).finally(function (r) {
                if (angular.isFunction(alwaysFunc)) {
                    alwaysFunc(r);
                }

                $rootScope.showLoading = false;

                $log.log("Always done: " + angular.toJson(r));
            });
        },
        showGlobalLoadingBox: function showGlobalLoadingBox() {
            $rootScope.showLoading = true;
        },
        hideGlobalLoadingBox: function hideGlobalLoadingBox() {
            $rootScope.showLoading = false;
        },
        go: function go(toPage) {
            $state.go(toPage); // toPage: defined in routes.js
        },
        checkSession: function checkSession() {
            var user = localStorageService.get(APP_CONST.LOGGED_IN_USER);
            var isSecured = angular.isDefined(user) && user !== null;

            // Update UI to show/ hide the secure content as needed.
            $rootScope.isSecured = isSecured;

            return isSecured;
        },
        clearSession: function clearSession() {
            $log.log('clearSession');
            localStorageService.clearAll();
            // _paq.push(["deleteCookies"]); // (2) deletes existing tracking cookies to start the new visit
            // _paq.push(['appendToTrackingUrl', 'new_visit=1']); // (1) forces a new visit
        },
        setLeftSidebarMenus: function setLeftSidebarMenus(menus) {
            localStorageService.set(APP_CONST.LEFT_SIDEBAR_MENUS, menus);
        },
        getLeftSidebarMenus: function getLeftSidebarMenus() {
            return localStorageService.get(APP_CONST.LEFT_SIDEBAR_MENUS);
        },
        apiHost: function apiHost() {
            var url = $window.location;
            var origin = url.origin;

            // develop environment in local
            if (origin === 'http://localhost:3000') {
                origin = 'http://localhost:9052';
            }

            return origin;
        },
        isEmptyString: function isEmptyString(str) {
            return angular.isUndefined(str) || str === null || /^\s*$/.test(str);
        },
        moreFunc: function moreFunc() {
            $log.log("call test from util...");
        }
    }; // end util

    return util;
}
