angular.module('dashboardApp').filter('linebreak', function () {
    return function (text) {
        if (/\n/.test(text)) {
            return text.replace(/\n/g, '<br>');
        }

        return text;
    };
}).filter('to_trusted', ['$sce', function ($sce) {
    return function (text) {
        return $sce.trustAsHtml(text);
    };
}]);